@tailwind base;
@tailwind components;
@tailwind utilities;

/* Подключение пиксельного шрифта */
@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&family=VT323&family=Pixelify+Sans&display=swap');

body {
  margin: 0;
  font-family: 'Press Start 2P', 'VT323', 'Pixelify Sans', sans-serif;
  background-color: #f7fafc;
  -webkit-font-smoothing: none;
  -moz-osx-font-smoothing: none;
  text-rendering: optimizeSpeed;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  flex-direction: column;
}

/* Контейнер */
.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 600px; /* Увеличил ширину контейнера */
  padding: 1rem;
  text-align: center;
}

/* Карточка */
.card {
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 2rem;
  width: 100%;
  max-width: 600px;
  text-align: center;
}

/* Заголовки (вопросы) */
h1, .centered-text h1 {
  font-size: 1.8rem; /* Увеличил размер шрифта */
  font-weight: bold;
  color: #333;
  margin-bottom: 1rem;
  text-align: center;
  font-family: 'Press Start 2P', cursive;
}

/* Обычный текст (ответы) */
p, .centered-text p {
  font-size: 1.3rem; /* Увеличил размер текста */
  color: #555;
  margin-bottom: 1.5rem;
  text-align: center;
  font-family: 'VT323', monospace;
}

/* Поле ввода */
.input-field {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid #000;
  border-radius: 4px;
  font-size: 1.2rem; /* Сделал текст в поле ввода больше */
  font-family: 'Pixelify Sans', sans-serif;
  margin-bottom: 1rem;
  transition: border-color 0.3s ease;
}

.input-field:focus {
  border-color: #4caf50;
  outline: none;
}

/* Кнопка */
.button {
  padding: 1rem 2rem;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 1.2rem; /* Увеличил размер кнопки */
  cursor: pointer;
  font-family: 'Press Start 2P', monospace;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: #388e3c;
}

.button:disabled {
  background-color: #484848;
  cursor: not-allowed;
}

/* Прогресс-бар */
.progress-bar {
  width: 100%;
  height: 12px;
  background-color: #4d4c4c;
  border-radius: 5px;
  margin-bottom: 1rem;
  border: 4px solid #25d031;
  position: relative;
  top: -5px;
}

/* Футер (ОСТАВЛЕН БЕЗ ИЗМЕНЕНИЙ) */
.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #f0f0f0;
  text-align: center;
  padding: 10px 0;
  font-size: 16px;
  color: #333;
  font-family: 'Arial', sans-serif;
  border-top: 1px solid #ddd;
}

.footer p {
  margin: 0;
  color: #555;
  font-size: 1rem;
  font-weight: 600;
}

.footer p a {
  color: #007bff;
  text-decoration: none;
  font-weight: 700;
}

.footer p a:hover {
  text-decoration: underline;
}

/* Медиа-запросы для мобильных */
@media (max-width: 768px) {
  .card {
    padding: 1rem;
    max-width: 400px;
  }

  h1, .centered-text h1 {
    font-size: 1.5rem; /* Немного уменьшил для мобилок */
  }

  p, .centered-text p {
    font-size: 1.1rem;
  }

  .button {
    font-size: 1rem;
    padding: 0.8rem 1.5rem;
  }
}

h3{
  font-size: 1.2rem;
  margin-bottom: 15px;
}